import React, { Component } from "react";
import "./Dashboard.css";
import { connect } from "react-redux";
import * as Actions from "../../store/Feature1/FeatureAction";
import * as Actions1 from "../../store/JobDescription/JobDescriptionAction";
import * as DashboardActions from "../../store/Dashboard/DashboardAction";
import { bindActionCreators } from "redux";
import hire from "../../assets/images/no-of-hire.svg";
import jd from "../../assets/images/no-of-jd.svg";
import reject from "../../assets/images/no-of-reject.svg";
import released from "../../assets/images/no-of-released.svg";
import joined from "../../assets/images/joined.svg";
import ongoing from "../../assets/images/ongoing-jd.svg";
import njoined from "../../assets/images/not-joined.svg";
import completed from "../../assets/images/complete-jd.svg";
import nego from "../../assets/images/negotiation.svg";
import Card from "../../components/Controls/Card/CommonCard";
import CalenderCard from "../../components/Controls/calenderCard/calenderCard";
import Button from "../../components/Controls/Button/Button";
// import TableDashboard from "../../components/Controls/Table/TableDashboard";
import TableComponent from "../../components/Controls/Table/sortableTable";
import { withRouter } from "react-router-dom";
// import LoadingBar from "react-top-loading-bar";
import moment from "moment";
import ReactGA from "react-ga";
export class Dashboard extends Component {
  state = {
    companyJoined: 0,
    interviewTaken: 0,
    negotiation: 0,
    noOfPositions: 0,
    noOfCandidates: 0,
    offerAccepted: 0,
    offerReleased: 0,
    shortlisted: 0,
    totalJd: 0,
    ongoingJd: 0,
    completedJd: 0,
    jobdata: [],
    page: 1,
    limit: 10,
    totalPage: 1,
    day: true,
    week: false,
    month: false,
    startDate: moment().subtract(30, "d"),
    endDate: moment(),
    // endDate: moment().add(5, "M"),
    graph: [],
    series: [
      {
        name: "Number of Hire",
        data: [],
      },
      {
        name: "Number of Rejected",
        data: [],
      },
      {
        name: "Ongoing JD",
        data: [],
      },
      {
        name: "Not Joined",
        data: [],
      },
      {
        name: "Joined Company",
        data: [],
      },
    ],
  };

  componentDidMount() {
    ReactGA.pageview(window.location.pathname);
    this.props.DashboardAction.getDashboardData({
      startDate: new Date(this.state.startDate).toISOString(),
      endDate: new Date(this.state.endDate).toISOString(),
    });
    this.props.JobDescriptionAction.getJobDescriptionData({
      limit: 0,
      page: 1,
    });
  }

  componentDidUpdate(prevProps, prevState) {
    if (
      (prevProps &&
        prevProps.JobDescriptionReducer &&
        prevProps.JobDescriptionReducer.getJobDescriptionData) !==
        (this.props.JobDescriptionReducer &&
          this.props.JobDescriptionReducer.getJobDescriptionData) &&
      this.props.JobDescriptionReducer.getJobDescriptionData &&
      this.props.JobDescriptionReducer.getJobDescriptionData.results &&
      this.props.JobDescriptionReducer.getJobDescriptionData.results.length > 0
    ) {
      let data = this.props.JobDescriptionReducer.getJobDescriptionData.results;
      let onGoing = 0,
        completed = 0;
      data.map((el) => {
        if (el && el.status === "ONGOING") {
          onGoing += 1;
        }
        if (el && el.status === "HIRED") {
          completed += 1;
        }
      });
      this.setState({
        totalJd:
          this.props.JobDescriptionReducer.getJobDescriptionData &&
          this.props.JobDescriptionReducer.getJobDescriptionData.totalResults,
        completedJd: completed,
        ongoingJd: onGoing,
      });
    }

    if (
      (prevProps &&
        prevProps.DashboardReducer &&
        prevProps.DashboardReducer.getDashboardData) !==
        (this.props.DashboardReducer &&
          this.props.DashboardReducer.getDashboardData) &&
      this.props.DashboardReducer.getDashboardData &&
      this.props.DashboardReducer.getDashboardData.data &&
      this.props.DashboardReducer.getDashboardData.data.length > 0
    ) {
      let dashData = this.props.DashboardReducer.getDashboardData.data;
      let data = [];
      let totalPage = this.state.totalPage,
        limit = this.state.limit;
      let jobData = [];
      dashData &&
        dashData.length > 0 &&
        dashData.map((item, index) => {
          jobData.push({
            jobname: item.jobTitle ? item.jobTitle : "",
            exp: item.experience ? item.experience : 0,
            hire: item.noOfPositions ? item.noOfPositions : 0,
            shortlist: item.shortlisted ? item.shortlisted : 0,
            interview: item.interviewTaken ? item.interviewTaken : 0,
            select: item.offerReleased ? item.offerReleased : 0,
            candidates: item.noOfCandidates ? item.noOfCandidates : 0,
            negotiation: item.negotiation ? item.negotiation : 0,
            companyJoined: item.companyJoined ? item.companyJoined : 0,
          });
        });
      data = jobData;
      totalPage =
        data.length % limit === 0
          ? parseInt(data.length / limit)
          : parseInt(data.length / limit + 1);
      this.setState({ totalPage: totalPage, jobdata: data });

      let companyJoined = 0,
        interviewTaken = 0,
        negotiation = 0,
        noOfPositions = 0,
        offerAccepted = 0,
        offerReleased = 0,
        shortlisted = 0,
        noOfCandidates = 0;
      dashData.forEach((item, index) => {
        companyJoined = item.companyJoined
          ? companyJoined + item.companyJoined
          : companyJoined;
        interviewTaken = item.interviewTaken
          ? interviewTaken + item.interviewTaken
          : interviewTaken;
        negotiation = item.negotiation
          ? negotiation + item.negotiation
          : negotiation;
        noOfPositions = item.noOfPositions
          ? noOfPositions + item.noOfPositions
          : noOfPositions;
        noOfCandidates = item.noOfCandidates
          ? noOfCandidates + item.noOfCandidates
          : noOfCandidates;
        offerAccepted = item.offerAccepted
          ? offerAccepted + item.offerAccepted
          : offerAccepted;
        offerReleased = item.offerReleased
          ? offerReleased + item.offerReleased
          : offerReleased;
        shortlisted = item.shortlisted
          ? shortlisted + item.shortlisted
          : shortlisted;
      });
      this.setState({
        companyJoined: companyJoined,
        interviewTaken: interviewTaken,
        negotiation: negotiation,
        noOfPositions: noOfPositions,
        offerAccepted: offerAccepted,
        offerReleased: offerReleased,
        shortlisted: shortlisted,
        noOfCandidates: noOfCandidates,
      });
    }
    if (
      (prevProps &&
        prevProps.DashboardReducer &&
        prevProps.DashboardReducer.getDashboardData) !==
        (this.props.DashboardReducer &&
          this.props.DashboardReducer.getDashboardData) &&
      this.props.DashboardReducer.getDashboardData &&
      this.props.DashboardReducer.getDashboardData.length === 0
    ) {
      this.setState({
        companyJoined: 0,
        interviewTaken: 0,
        negotiation: 0,
        noOfPositions: 0,
        noOfCandidates: 0,
        offerAccepted: 0,
        offerReleased: 0,
        shortlisted: 0,
        totalJd: 0,
        ongoingJd: 0,
        completedJd: 0,
        jobdata: [],
        page: 1,
        limit: 10,
        totalPage: 1,
      });
    }
    if (
      (prevState.startDate !== this.state.startDate && this.state.startDate) ||
      (prevState.endDate !== this.state.endDate && this.state.endDate)
    ) {
      this.props.DashboardAction.getDashboardData({
        startDate: new Date(this.state.startDate).toISOString(),
        endDate: new Date(this.state.endDate).toISOString(),
      });
    }
  }

  day = () => {
    this.setState({ day: true, week: false, month: false });
  };
  week = () => {
    this.setState({ day: false, week: true, month: false });
  };
  month = () => {
    this.setState({ day: false, week: false, month: true });
  };

  handleProgressChange = () => {
    if (this.state.progress < 90 && !this.state.stop) {
      this.setState({
        progress: this.state.progress + 15,
      });
    }
  };

  setStartDate = (date) => {
    this.setState({
      startDate: date.selection.startDate,
      endDate: date.selection.endDate,
    });
  };

  // setEndDate = (date) => {
  //   this.setState({ endDate: date });
  // };

  handleBackPagination = async (e) => {
    if (this.state.page != 1) {
      await this.setState({ page: this.state.page - 1 });
    }
  };
  handleNextPagination = async () => {
    if (this.state.page != this.state.totalPage) {
      await this.setState({ page: this.state.page + 1 });
    }
  };

  handlePagination = async (e) => {
    await this.setState({ page: parseInt(e) });
  };

  setLimit = (e) => {
    this.setState({
      limit: e,
      page: 1,
    });
  };

  render() {
    let inboundDatacolumn = [
      {
        Header: () => <div style={{ textAlign: "center" }}>Job Name</div>,
        accessor: "jobname",
        minWidth: 200,
        style: { whiteSpace: "initial" },
        Cell: (row) => (
          <div
            style={{
              textAlign: "left",
            }}
          >
            {/* {row.index + 1}.&nbsp; {row.value} */}
            {(this.state.page - 1) * this.state.limit +
              row.index +
              1}.&nbsp; {row.value}
          </div>
        ),
      },
      {
        Header: () => <div style={{ textAlign: "center" }}>Experience</div>,
        minWidth: 90,
        accessor: "exp",
        style: { whiteSpace: "initial" },
        Cell: (row) => (
          <div
            style={{
              textAlign: "center",
            }}
          >
            {row.value}
          </div>
        ),
      },
      {
        Header: () => (
          <div style={{ textAlign: "center", whiteSpace: "initial" }}>
            No of Positions
          </div>
        ),
        minWidth: 80,
        accessor: "hire",
        style: { whiteSpace: "initial" },
        Cell: (row) => (
          <div
            style={{
              textAlign: "center",
            }}
          >
            {row.value}
          </div>
        ),
      },
      {
        Header: () => (
          <div style={{ textAlign: "center", whiteSpace: "initial" }}>
            Profile Sourcing
          </div>
        ),
        minWidth: 80,
        accessor: "candidates",
        style: { whiteSpace: "initial" },
        Cell: (row) => (
          <div
            style={{
              textAlign: "center",
            }}
          >
            {row.value}
          </div>
        ),
      },
      {
        Header: () => (
          <div style={{ textAlign: "center", whiteSpace: "initial" }}>
            Interview Taken
          </div>
        ),
        minWidth: 80,
        accessor: "interview",
        style: { whiteSpace: "initial" },
        Cell: (row) => (
          <div
            style={{
              textAlign: "center",
            }}
          >
            {row.value}
          </div>
        ),
      },
      {
        Header: () => (
          <div style={{ textAlign: "center", whiteSpace: "initial" }}>
            Shortlist
          </div>
        ),
        minWidth: 75,
        accessor: "shortlist",
        style: { whiteSpace: "initial" },
        Cell: (row) => (
          <div
            style={{
              textAlign: "center",
            }}
          >
            {row.value}
          </div>
        ),
      },
      {
        Header: () => <div style={{ textAlign: "center" }}>Selected</div>,
        minWidth: 80,
        accessor: "select",
        style: { whiteSpace: "initial" },
        Cell: (row) => (
          <div
            style={{
              textAlign: "center",
            }}
          >
            {row.value}
          </div>
        ),
      },
      // {
      //   Header: () => (
      //     <div style={{ textAlign: "center", whiteSpace: "initial" }}>
      //       Negotiation
      //     </div>
      //   ),
      //   minWidth: 80,
      //   accessor: "negotiation",
      //   style: { whiteSpace: "initial" },
      //   Cell: (row) => (
      //     <div
      //       style={{
      //         textAlign: "center",
      //       }}
      //     >
      //       {row.value}
      //     </div>
      //   ),
      // },
      {
        Header: () => <div style={{ textAlign: "center" }}>Joined</div>,
        minWidth: 80,
        accessor: "companyJoined",
        style: { whiteSpace: "initial" },
        Cell: (row) => (
          <div
            style={{
              textAlign: "center",
            }}
          >
            {row.value}
          </div>
        ),
      },

      // {
      //   Header: () => (
      //     <div style={{ textAlign: "center", whiteSpace: "initial" }}>
      //       Start Date
      //     </div>
      //   ),
      //   minWidth: 70,
      //   accessor: "time",
      //   style: { whiteSpace: "initial" },
      //   Cell: (row) => (
      //     <div
      //       style={{
      //         textAlign: "center",
      //       }}
      //     >
      //       {row.value}
      //     </div>
      //   ),
      // },
    ];

    const {
      companyJoined,
      interviewTaken,
      negotiation,
      noOfPositions,
      offerAccepted,
      offerReleased,
      shortlisted,
      noOfCandidates,
      totalJd,
      ongoingJd,
      completedJd,
    } = this.state;
    let data = [];
    let totalPage = this.state.totalPage,
      limit = this.state.limit;

    if (this.state.jobdata && this.state.jobdata.length > 0) {
      if (this.state.page == 1) {
        data = this.state.jobdata.slice(0, this.state.limit);
      } else {
        let data1 = data;
        let start = this.state.limit * (this.state.page - 1);
        data1 = this.state.jobdata.slice(start, start + this.state.limit);
        data = data1;
      }
    }
    return (
      <div className='dash-page'>
        {/* {!this.state.stop ? (
          <LoadingBar
            color="#04724d"
            height={5}
            progress={this.state.progress}
            onLoaderFinished={() => {
              this.setState({ progress: 0 });
            }}
          />
        ) : null} */}

        {/* Card Div */}
        <div className='dash-card-smooth'>
          <div className='dash-cards-wrap'>
            <div className='dash-card-row1'>
              <div className='dcard'>
                <div>
                  <CalenderCard
                    startDate={this.state.startDate}
                    endDate={this.state.endDate}
                    handleSelectDate={(date) => this.setStartDate(date)}
                    className='dashCal'
                  />
                </div>
              </div>
            </div>
            <div className='dash-card-row'>
              <div className='dcard-1'>
                <Card
                  // cardTitle={no_of_jd}
                  cardTitle={totalJd}
                  imgWidth={"58px"}
                  imgHeight={"58px"}
                  titleFontSize={"px"}
                  cardImage={jd}
                  cardContent='No of JD'
                  // cardContent="Company Joined"
                  cardPadding='20px'
                />
              </div>
              <div className='dcard-1'>
                <Card
                  // cardTitle={ongoingjd}
                  cardTitle={ongoingJd}
                  imgWidth={"58px"}
                  imgHeight={"58px"}
                  titleFontSize={"px"}
                  cardImage={ongoing}
                  cardContent='Ongoing JD'
                  // cardContent="Interview Taken"
                  cardPadding='20px'
                />
              </div>
              <div className='dcard-1'>
                <Card
                  // cardTitle={completedjd}
                  cardTitle={completedJd}
                  imgWidth={"58px"}
                  imgHeight={"58px"}
                  titleFontSize={"px"}
                  cardImage={completed}
                  cardContent='Complete JD'
                  // cardContent="Negotiation"
                  cardPadding='20px'
                />
              </div>
            </div>
            <div className='dash-card-row'>
              <div className='dcard-1'>
                <Card
                  // cardTitle={no_of_hire}
                  cardTitle={noOfPositions}
                  imgWidth={"58px"}
                  imgHeight={"58px"}
                  titleFontSize={"px"}
                  cardImage={hire}
                  cardContent='No of Positions'
                  cardPadding='20px'
                />
              </div>
              <div className='dcard-1'>
                <Card
                  // cardTitle={no_of_rejected}
                  cardTitle={offerAccepted}
                  imgWidth={"58px"}
                  imgHeight={"58px"}
                  titleFontSize={"px"}
                  cardImage={reject}
                  // cardContent="No of Reject"
                  cardContent='Offer Accepted'
                  cardPadding='20px'
                />
              </div>
              <div className='dcard-1'>
                <Card
                  // cardTitle={negotiation}
                  cardTitle={offerReleased}
                  imgWidth={"58px"}
                  imgHeight={"58px"}
                  titleFontSize={"px"}
                  cardImage={nego}
                  // cardContent="Negotiation"
                  cardContent='Offer Released'
                  cardPadding='20px'
                />
              </div>
            </div>
            <div className='dash-card-row'>
              <div className='dcard-1'>
                <Card
                  cardTitle={noOfCandidates}
                  imgWidth={"58px"}
                  imgHeight={"58px"}
                  titleFontSize={"px"}
                  cardImage={released}
                  cardContent='Profile Sourcing'
                  cardPadding='20px'
                />
              </div>
              <div className='dcard-1'>
                <Card
                  cardTitle={companyJoined}
                  imgWidth={"58px"}
                  imgHeight={"58px"}
                  titleFontSize={"px"}
                  cardImage={joined}
                  cardContent='Joined'
                  cardPadding='20px'
                />
              </div>
              <div className='dcard-1'>
                <Card
                  cardTitle={interviewTaken}
                  imgWidth={"58px"}
                  imgHeight={"58px"}
                  titleFontSize={"px"}
                  cardImage={njoined}
                  cardContent='Inerview Taken'
                  cardPadding='20px'
                />
              </div>
            </div>
          </div>
        </div>

        <div className='dashTable'>
          <TableComponent
            className={"dashboard-table"}
            // data={this.state.jobdata}
            data={data}
            columns={inboundDatacolumn}
            sortable={true}
            showBottomPagination={true}
            // pageSize={this.state.limit}
            pageSize={limit}
            setPageSize={(e) => this.setLimit(e)}
            gotoPage={(e) => this.handlePagination(e)}
            pageNumber={this.state.page ? this.state.page : 1}
            // totalPage={this.state.totalPage}
            totalPage={totalPage}
            handlePrev={this.handleBackPagination}
            handleNext={this.handleNextPagination}
          />
        </div>
      </div>
    );
  }
}

const mapStateToProps = (state) => {
  return {
    FeatureReducer: state.FeatureReducer,
    DashboardReducer: state.DashboardReducer,
    JobDescriptionReducer: state.JobDescriptionReducer,
  };
};

const mapDispatchToProps = (dispatch) => {
  return {
    FeatureAction: bindActionCreators(Actions, dispatch),
    DashboardAction: bindActionCreators(DashboardActions, dispatch),
    JobDescriptionAction: bindActionCreators(Actions1, dispatch),
  };
};

export default connect(
  mapStateToProps,
  mapDispatchToProps
)(withRouter(Dashboard));
