import React from "react";
import "./CommonCard.css";

const CommonCard = (props) => {
  return (
    <div className="com-card" onClick={props.onClick}>
      <div className="cardContent">
        <div>
          <img
            width={props.imgWidth ? props.imgWidth : "80px"}
            height={props.imgHeight ? props.imgHeight : "80px"}
            src={props.cardImage}
            style={{
              marginRight: "auto",
            }}
          />
        </div>

        <div>
          <h1 className="cardTitle">{props.cardTitle}</h1>

          <p className="cardContentText">{props.cardContent}</p>
        </div>
      </div>
    </div>
  );
};

export default CommonCard;
